@import url(https://cdn.syncfusion.com/ej2/material.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.card-list {
  display: -webkit-flex;
  display: flex;
  background-color: #e9e9e9;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.card {
  background-color: #61dafb;
  width: 15rem;
  border-radius: 2rem;
  text-align: left;
  padding: 2rem;
}

.card h3 {
  text-align: center;
}

.fc {
  /* the calendar root */
  height: 800px;
  margin: 0 auto;
}

.fc {
  /* the calendar root */
  height: 700px;
  width: 100%;
  margin: 0 auto;
}

.fc-event {
  cursor: pointer;
}

.timerContainer {
  width: 475px;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 800px;
  background-color: #d3d3d3;
  padding: 20px;
  border-radius: 20px;
}

.form label {
  margin-bottom: 30px;
}

.form input {
  display: block;
  margin-top: 10px;
}

.form .color {
  color: black;
}

.staff-main {
  display: -webkit-flex;
  display: flex;
}
